export default {
  data() {
    return {
      data: {},
      description: {},
    };
  },
  methods: {
    getData() {
      this.axios({
        method: "get",
        url: "https://raw.githubusercontent.com/Rosette-xii/vue-mysite/main/src/assets/static/data.json",
      })
        .then((res) => {
          this.data = res.data.graphicData[this.$route.query.id - 1];
          this.description = this.data.description;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    window.scrollTo(0, 0);
    this.getData();
  },
};
